import {login} from '../../utils/api.js'
import {mapGetters} from 'vuex'
import md5 from '../../utils/md5.js'
export default {
	data() {
		return {
			loading:false,
			userObj:{
				mobileTel:'',
				passWord:'',
				openId:''
			},
			disabled:false
		}
	},
	computed:{
		...mapGetters([
			'openID'
		])
			
	},
	onLoad() {
	},
	methods: {
		submit(){
			if(this.userObj.mobileTel == '' || this.userObj.mobileTel.length != 11){
				uni.showToast({
					title: '请输入正确的手机号',
					icon:'none',
					duration: 2000
				})
				return false
			}
			if(this.userObj.passWord == ''){
				uni.showToast({
					title: '请输入账号密码',
					icon:'none',
					duration: 2000
				})
				return false
			}
			this.loading = true
			this.disabled = true
			this.userObj.passWord = md5.hex_md5(this.userObj.passWord).toUpperCase()
			this.userObj.passWord = md5.hex_md5(this.userObj.passWord).toUpperCase()   //md5加密转大写
			this.userObj.openId = this.openID;
			login({...this.userObj}).then(res =>{
				this.$store.commit('setUserInfo',JSON.stringify(res.dtData[0]))
				uni.switchTab({
					url: '/pages/Home/Home'
				})
				this.disabled = false
				this.loading = false



			}).catch(err =>{
				this.loading = false
				this.disabled = false
			})
		}
	}
}
